import React from 'react'
import { Link } from 'react-router-dom'

const Footer = () => {
  const description = {
    es: '¿Estas buscando cocina japonesa y china ? ¡Con Asia Restaurantes estás en el lugar correcto! Sumérgete en la cultura asiática y prueba todos nuestros platos.',
    it: 'Stai cercando cucina Giapponese e cucina Cinese a domicilio? Con Ristorante Asia sei nel posto giusto! Immergiti nella cultura asiatica e prova tutti i nostri piatti.'
  }
  return (
    <div className='footer'>
      <img alt='restaurant' />
      <div className='restaurant'>
        <div className='name font2'>Asia Restaurants</div>
        <div className='description font3 grey'>{description.es}</div>
      </div>
      <div className='buttons'>
        <div className='navbar-home'>
          <Link to='/'>HOME</Link>
        </div>
        <div className='navbar-menu'>
          <Link to='/menu'>MENU</Link>
        </div>
        <div className='navbar-restaurant'>
          <Link to='/restaurant'>RISTORANTI</Link>
        </div>
      </div>
      <div className='copy font3'>© 2020 Asia Restaurants. All Rights Reserved</div>
    </div>
  )
}

export default Footer